.container {
  background-color: #f2f2f2;
  border: 1px solid #ccc;
  border-radius: 5px;
  max-width: 400px;
  margin: 100px auto;
  padding: 20px;
}

h2 {
  text-align: center;
}

.form-group {
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  display: block;
}

input[type="text"], input[type="email"], input[type="password"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  padding: 8px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 10px 15px;
}

button[type="submit"]:hover {
  background-color: #45a049;
}

